import { Component } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { InputFieldComponent } from '../../../shared/components/input-field/input-field.component';
import { CommonModule } from '@angular/common';
import { path } from '../../../models/routes.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  standalone: true,
  imports: [ReactiveFormsModule, InputFieldComponent, CommonModule],
  templateUrl: './register.component.html',
  styleUrl: './register.component.scss',
})
export class RegisterComponent {
  public formGroup: FormGroup;
  private LOGIN = `/${path.LOGIN}`;

  public personalDetailsFields = [
    { placeholder: 'First Name', type: 'text', controlName: 'firstName' },
    { placeholder: 'Last Name', type: 'text', controlName: 'lastName' },
  ];

  public businessDetailsFields = [
    { placeholder: 'Business Name', type: 'text', controlName: 'businessName' },
    { placeholder: 'Email', type: 'email', controlName: 'email' },
    { placeholder: 'Phone Number', type: 'text', controlName: 'phoneNumber' },
  ];

  constructor(private formBuilder: FormBuilder, private router: Router) {
    this.formGroup = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      businessName: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      phoneNumber: ['', [Validators.required]],
    });
  }

  onSubmit() {
    if (this.formGroup.valid) {
      const formData = this.formGroup.value;

      console.log(formData);
    }
  }

  onCancel() {
    this.router.navigate([this.LOGIN]);
  }
}
